export default {
	data() {
		return {
			shopInfo: {},
			initTab: 0,
			years: [
				[],
				[]
			],
			initYear:null,
			initMonth:null,
			tabType: [{
					id: 0,
					name: "未支付"
				},
				{
					id: 1,
					name: "已支付"
				},
			],
			yearsIndex1: 0,
			yearsIndex2: 0
		}
	},
	onShow() {
		
		const date = new Date()
		const years = this.years[0]
		const year = date.getFullYear()
		const months = this.years[1]
		const month = date.getMonth() + 1
		
		this.initYear = year
		this.initMonth = month
		for (let i = 2019; i <= date.getFullYear(); i++) {
			years.push(i)
		}
		for (let i = 1; i <= 12; i++) {
			months.push(i)
		}
		this.years[0] = years
		this.years[1] = months;
		console.log(this.years)
		this.getTopInfo(this.initTab,year,month)
	},
	methods: {
		tabSw(idx) {
			this.initTab = idx;
			this.getTopInfo(idx,this.initYear,this.initMonth)
		},
		go_details() {
			uni.navigateTo({
				url: "../shopDetails/shopDetails"
			})
		},
		yearChange: function(e) {
			console.log(e)
			//获得对象的 detail的 value
			//通过数组的下标改变显示在页面的值
			console.log(e)
			this.yearsIndex1 = e.detail.value[0];
			this.yearsIndex2 = e.detail.value[1];
			this.initYear = this.years[0][e.detail.value[0]]
			this.initMonth = this.years[1][e.detail.value[1]]
			this.getTopInfo(this.initTab,this.initYear,this.initMonth)

		},
		async getTopInfo(status,year,month) {
			try {
				let data = await this.$wxhttp.post({
					url: '/index/v1/queryUserSupplyOrderList',
					data: {
						status: status,
						year:year,
						month:month,
					}
				})
				this.shopInfo = data.data;
			} catch (e) {
				console.log(e)
			}
		},
	}
}
